/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-create-block-askaccordionfaq {
	.block-editor-block-list__block.wp-block{
		max-width: 100%;
	}
	.ask {
		.ask-tab-wrapper {
			padding: 0;
		}

		.ask__tab__question::before {
			content: inherit;
		}
	}
	.ask__tab__content {
		height: auto;
		p {
			height: auto;
		}
	}
	.ask__tab__question {
		padding: 0 !important;
		p {
			padding: 14px 16px;
		}
	}
	.block-editor-block-list__layout {
		// border: 1px solid var(--color-primary);
		border-radius: 5px;
		overflow: auto;
		max-height: 340px;
		scrollbar-width: thin;
		&::-webkit-scrollbar {
			width: 0.5em;
			height: 0.5em;
		}
		&::-webkit-scrollbar-track {
			background-color: var(--color-tertiary);
			margin-block: 0.5em;
		}
		&::-webkit-scrollbar-thumb {
			background: var(--color-primary);
		}
		&::-webkit-scrollbar-thumb:hover {
			opacity: 0.2;
		}
		@media screen and (max-width: "850px") {
			max-height: 200px;
		}
		.block-editor-block-list__block {
			margin-top: 0;
			margin-bottom: 0;
			// &:not(:last-child) {
			// 	border-bottom: 1px solid var(--color-primary);
			// }
		}
	}
	.ask__container {
		padding: 0 !important;
	}
}

.wp-block-create-block-boilerplate {
	border: 1px dotted rgb(1, 1, 1);
}

.faq-section {
	max-width: 800px;
	margin: 0 auto;
}

.faq-item {
	list-style: none;
	border-bottom: 1px solid var(--color-tertiary);
}

.faq-question {
	padding: 10px;
	cursor: pointer;
}

.faq-answer {
	padding: 10px;
	display: none;
}

.faq-item.ask__active .faq-answer {
	display: block;
	max-height: inherit;
}

.ask {
	.accordion-header {
		background-color: #eee;
		color: #333;
		cursor: pointer;
		padding: 18px;
		width: 100%;
		text-align: left;
		border: none;
		outline: none;
		transition: background-color 0.3s ease;
		.active {
			background-color: #ccc;
		}
	}

	.accordion-content {
		padding: 18px;
		display: none;
		background-color: #f9f9f9;
		overflow: hidden;
		&.active {
			display: block;
		}
	}
}

.theme-dark {
	.ask__accordion__panel {
		button {
			color: var(--color-light)
		}
	}
}
